@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');

.app {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin-top: 5%;
    font-family: Inter;
}

.profile-avatar {
    text-align: center;
}

.profile-avatar #profile__img {
    margin-bottom: 20px;
}

.profile-avatar #share_btn {
    position: absolute;
    right: 30%;
}

.link-section {
    margin-top: 5%;
    width: 100%;
}

.link-section .link-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.link-section .link-buttons button {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1180px;
    width: 90%;
    padding: 24px 32px;
    margin: 20px 0px;
    background: #EAECF0;
    border: 1px solid #EAECF0;
    border-radius: 8px;
}

.link-section .link-buttons button:last-child {
    display: none;
}

.link-buttons button a {
    color: black;
    text-decoration: none;
    font-family: Inter;
    font-weight: 500;
    font-size: 18px;
}

.social_links {
    margin-top: 10px;
    margin-bottom: 5%;
}

.social_links .wrapper {
    width: 70px;
    display: flex;
    justify-content: space-between;
}

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    border-top: 1px solid rgb(199, 198, 198);
    padding-top: 20px;
    margin-top: 3%;
    flex-wrap: wrap;
    gap: 20px;
}

@media (max-width: 460px) {
    .profile-avatar #share_btn  {
        right: 10%;
    }
}